<template>
    <div :class="['gamepad-button', type]">
        <p v-if="type=='circle'">○</p>
        <p v-else-if="type=='square'">□</p>
        <p v-else-if="type=='cross'">X</p>
        <p v-else-if="type=='triangle'">△</p>
    </div>
</template>

<script>
export default {
    props: ['type']
}
</script>

<style>
.gamepad-button{
   background-color: #414449;
   border-radius: 100%;
   width: 70px;
   height: 70px;
   text-align: center;
}

.square{
    line-height: 60px;
    font-size: 50px;
    color: #e771f2;
}

.circle{
    line-height: 60px;
    font-size: 60px;
    color: #e02323;
}

.cross{
    line-height: 70px;
    font-size: 50px;
    color: #5178ba;   
}

.triangle{
    line-height: 50px;
    font-size: 60px;
    color: #39b21e;
}

</style>




